import { Box } from '@mui/material'
import { create } from 'zustand'
import { useShallow } from 'zustand/react/shallow'

import { getTablerIcon, InitialsIcon } from 'helpers/icons'
import { Person } from 'helpers/person'

type DebtPerson2State = {
    person: Person | undefined
}

type DebtPerson2Actions = {
    isActive: () => boolean

    setPerson: (person: Person | undefined) => void
    reset: () => void
}

const initialState: DebtPerson2State = {
    person: undefined,
}

export const useDebtPerson2Store = create<DebtPerson2State & DebtPerson2Actions>()((set, get) => ({
    ...initialState,

    isActive: () => {
        const { person } = get()
        return person !== undefined
    },

    setPerson: (person: Person | undefined) => {
        const { person: currentPerson } = get()
        if (currentPerson === person) {
            set(() => ({ person: undefined }))
        } else {
            set(() => ({ person }))
        }
    },
    reset: () => set(initialState),
}))

export const DebtPerson2 = () => {
    const {
        person: currentPerson,
        setPerson,
        reset,
    } = useDebtPerson2Store(useShallow((state) => state))

    const people = Object.values(Person).filter((person) => person !== Person.Everyone)

    return (
        <Box
            sx={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                marginX: 2,
                width: '100%',
                fontSize: '14px',
            }}>
            <Box
                sx={{
                    'display': 'flex',
                    'justifyContent': 'center',
                    'alignItems': 'center',
                    'borderRadius': '100%',
                    '&:active': {
                        backgroundColor: '#FBBC04',
                    },
                    'transition': 'background-color 0.1s',
                }}
                onClick={() => reset()}>
                {getTablerIcon({ name: 'IconX' })}
            </Box>
            {people.map((person) => {
                return (
                    <Box
                        key={'debt-person-2-' + person}
                        sx={{
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            fontSize: '12px',
                        }}
                        onClick={() => {
                            setPerson(person as Person)
                        }}>
                        <InitialsIcon
                            person={person as Person}
                            sx={
                                currentPerson === (person as Person)
                                    ? undefined
                                    : { color: 'black', backgroundColor: 'lightgray' }
                            }
                        />
                    </Box>
                )
            })}
        </Box>
    )
}
