import { Box, Typography } from '@mui/material'
import { useShallow } from 'zustand/react/shallow'

import { FormattedMoney } from 'helpers/currency'
import { Person } from 'helpers/person'
import { useGustavoStore } from 'views/gustavo'
import { useDebtPerson1Store } from 'components/summary/summary-items/debt/debt-person-1'
import { useDebtPerson2Store } from 'components/summary/summary-items/debt/debt-person-2'
import { useEffect, useState } from 'react'
import { getTablerIcon, InitialsIcon } from 'helpers/icons'
import { UserCircle } from '@phosphor-icons/react'

export const SummaryDebt = () => {
    const { debtMapByPerson } = useGustavoStore(useShallow((state) => state))
    const { person: person1 } = useDebtPerson1Store(useShallow((state) => state))
    const { person: person2 } = useDebtPerson2Store(useShallow((state) => state))

    // const person1 = Person.Ivan
    // const person2 = Person.Jenny
    const [debt, setDebt] = useState(0)

    useEffect(() => {
        if (person1 && person2) {
            const debt = debtMapByPerson.get(person1)?.get(person2) || 0
            setDebt(debt)
        } else {
            setDebt(0)
        }
    }, [person1, person2])

    const debtAbsolute = Math.abs(debt)
    const debtString = FormattedMoney('USD', 2).format(debtAbsolute)

    return (
        <Box
            sx={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                margin: 1,
                padding: 2,
                border: '1px solid #FBBC04',
                borderRadius: '20px',
                backgroundColor: 'white',
            }}>
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    width: 120,
                    height: 120,
                }}>
                {person1 ? (
                    <InitialsIcon
                        person={person1}
                        sx={{
                            width: 100,
                            height: 100,
                            fontSize: 32,
                        }}
                    />
                ) : (
                    <UserCircle size={120} />
                )}
            </Box>
            <Box
                sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}>
                <Typography
                    sx={{
                        fontSize: 18,
                        fontWeight: 'bold',
                    }}>
                    {debtString}
                </Typography>
                <Box>
                    {debt < 0 && getTablerIcon({ name: 'IconHandFingerLeft' })}
                    {debt > 0 && getTablerIcon({ name: 'IconHandFingerRight' })}
                </Box>
            </Box>
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    width: 120,
                    height: 120,
                }}>
                {person2 ? (
                    <InitialsIcon
                        person={person2}
                        sx={{
                            width: 100,
                            height: 100,
                            fontSize: 32,
                        }}
                    />
                ) : (
                    <UserCircle size={120} />
                )}
            </Box>
        </Box>
    )
}
